	import uniPopup from "@/components/uni-popup/uni-popup.vue"
	import keyboardPackage from "../keyboard-package/keyboard-package.vue"
	export default {
		props:{
			length:{//长度只允许为6和4
				type:Number,
				default:6,
				validator(val){
					if(val!==6 && val !==4){
						return false;
					}
					return true;
				}
			},
			gutter:{
				type:Number,
				default:0
			},
			list:{
				type:Array,
				default:function(){
					return [];
				}
			}
		},
		data() {
			return {
				flag:true,
				hiddenmodalput:true,
				tip:'请输入您的密码'
			};
		},
/* 		computed:{
			keyboardPackage
		}, */
		methods:{
			close: function() {
               this.$emit('clo');
			   this.$refs.keyboardPackage.close();
			},
			forget: function() {
			  var that = this;
			  uni.redirectTo({
			    url: '../../my/change-otherPassword/change_otherPassword'
			  })
			},
			formatMargin(gutter){
				return 0 + ' ' + 300+'rpx';
			}
		}
	}
